"use client"
import { useState, useEffect, useRef } from "react"
import { supabase } from "@/utils/supabase/alSupabase"
import { useRouter } from "next/navigation";
import { useAuth } from "../things/hooks/useAuth";
import { useMessages } from "../things/hooks/useMessages";
import { sighOut } from "../things/utils/auth";

export default function Page() {
    const router = useRouter();

    const {user, profile, loading: authLoading} = useAuth();
    
    const {inbox, sent, sendMessage: send, loading: messageLoading} = useMessages(user?.id || null);


    const [isOpen, setIsOpen] = useState(false);

    const idRef = useRef<HTMLInputElement>(null);
    const textRef = useRef<HTMLInputElement>(null);
    const [isAnon, setIsAnon] = useState(false);

    // ответ на сообщение
    const [replyToUserId, setreplyToUserId] = useState<{id: string; isAnon: boolean} | null>(null); //id
    const [isReply, setIsReply] = useState(false);

    const [tab, setTab] = useState<"inbox" | "sent">("inbox");
    
    // автовставка id
    useEffect(() => {
        if (isOpen && idRef.current) {
            idRef.current.value = replyToUserId?.id || ''
        }
    }, [isOpen, replyToUserId])
    
    const handleSendMessage = async () => {
        const idInput = idRef.current?.value || ""
        const text = textRef.current?.value || ""

        if (!idInput || !text){
            alert('все поля заполни, сладкий!')
            return
        }
        
        try {
            if (isReply){
                const sendMessage = replyToUserId?.isAnon
                    ? { userId: replyToUserId?.id}
                    : { publicId: replyToUserId?.id}
    
                await send(sendMessage, text, isAnon)
            }
            else {
                await send({publicId: idInput}, text, isAnon)
            }

            idRef.current!.value = ""
            textRef.current!.value = ""
            setIsOpen(false)
        } catch (error: any) {
            console.log(error.message || 'Error sending message');
        }
        
    }
    
    const handleSignOut = async () => {
        await sighOut()
        router.push('/anonlove/auth')
    }

    if (authLoading) {
        return (
            <div className="bg-black min-h-screen flex items-center justify-center text-white">
                Загрузка...
            </div>
        );
    }

    return (
        <div className="relative bg-black min-h-screen flex flex-col">
            {user ? (
                <header className="bg-[#8f184f] text-white p-1 flex justify-between items-center shadow-md">
                    <div className="flex items-center gap-3">
                        <div className="w-10 h-10 rounded-full bg-red-500 flex items-center justify-center text-lg font-bold">
                            {profile?.public_id.charAt(1).toUpperCase()}
                        </div>
                        <div className="flex flex-col">
                            <span className="font-semibold">{profile?.public_id}</span>
                        </div>
                    </div>
                    <button
                        className="bg-white/10 hover:bg-white/20 text-white px-4 py-1 rounded-md transition"
                        onClick={handleSignOut}
                    >
                        Выйти
                    </button>
                </header>
            ) : (
                <header className="bg-gray-900 text-white p-4 shadow-md">
                    <span>Вы не залогинены</span>
                </header>
            )}

            {/* сообщения */}
            <main 
                className="relative flex-1 md:p-2 p-1 text-white flex md:flex-row flex-col"
            >
                <div 
                    className="flex flex-col bg-[#12080b] border-2 border-[#8f184f] rounded-lg md:w-1/2 min-h-full "
                >
                    <div className="flex ">
                        <button 
                            onClick={() => setTab("inbox")} 
                            className="bg-white/10 p-2 rounded-md w-1/2 text-center flex items-center gap-1"
                        >
                            <span className="medium">Сообщения</span>
                            <span 
                                className={`w-3 h-3 rounded-full border transition
                                    ${tab === "inbox"
                                        ? "bg-white border-white"
                                        : "border-white/50"
                                    }    
                                `}
                            />
                        </button>

                        <button 
                            onClick={() => setTab("sent")} 
                            className="bg-white/10 p-2 rounded-md w-1/2 text-center flex items-center gap-1"
                        >
                            <span className="medium">Отправленные</span>
                            <span 
                                className={`w-3 h-3 rounded-full border transition
                                    ${tab === "sent"
                                        ? "bg-white border-white"
                                        : "border-white/50"
                                    }    
                                `}
                            />
                        </button>
                    </div>

                    <div className="flex flex-col items-start gap-2 p-2 overflow-y-auto h-[calc(100vh-120px)]">
                        {(tab === "inbox" ? inbox : sent).map((message) => {
                            const isInbox = tab === 'inbox';

                            const avatar = isInbox
                                ? message.display_name.charAt(0).toUpperCase()
                                : profile.public_id.charAt(1).toUpperCase();
                            
                            const nameLabel = isInbox
                                ? message.display_name
                                : `${profile.public_id} > ${message.to_user.public_id}`
                                

                            const bodyText = "тебе тут что то пришло, малышка"

                            return (
                                <div
                                    key={message.id}
                                    className="flex flex-col items-start relative w-full hover:bg-white/10 transition "
                                > 
                                    <div
                                        className={`flex items-center gap-2 p-2 cursor-pointer `}
                                        onClick={() => isInbox && router.push(`lovecard#${message.id}`)}
                                    >
                                        <div
                                            className="md:w-10 md:h-10 w-8 h-8
                                                    rounded-full bg-red-500 flex items-center justify-center text-lg font-bold"
                                        > 
                                            {avatar}
                                        </div>

                                        <div
                                            className={`flex flex-col`}
                                        > 
                                            <span className="font-semibold medium">{nameLabel}</span>
                                            <span className="small line-clamp-2">{bodyText}</span>
                                        </div>
                                    </div>

                                    {/* svg */}
                                    {isInbox && (
                                        <button className="absolute top-1/2 right-1 -translate-y-1/2 "
                                                onClick={() => {
                                                    setreplyToUserId({
                                                        id: message.from_user || message.from_public_id,
                                                        isAnon: message.is_anon
                                                    })
                                                    setIsReply(true)
                                                    setIsOpen(true)
                                                }}
                                        > 
                                            <svg xmlns="http://www.w3.org/2000/svg" width="18" height="18" viewBox="0 0 14 14"><title></title><g fill="none" stroke="currentColor" strokeLinecap="round" strokeLinejoin="round"><path d="M7.5.5h-5a1 1 0 0 0-1 1v9l-1 3l4-1h8a1 1 0 0 0 1-1v-5"/><path d="m8.363 8.137l-3 .54l.5-3.04l4.73-4.71a.999.999 0 0 1 1.42 0l1.06 1.06a1.001 1.001 0 0 1 0 1.42z"/></g></svg>
                                        </button>
                                    )}

                                    <div className="w-full bg-white/20 h-[1px]"></div>
                                </div>
                            )
                        })}
                    </div>

                </div>

                <div 
                    className="flex w-1/2 justify-center items-center
                                md:static absolute bottom-0 right-0
                    ">
                    <AnimatedButton
                    openModal={() => {
                        setreplyToUserId(null)
                        setIsOpen(true)
                        setIsReply(false)
                    }}
                    >
                        Давай отправим что нибудь!
                    </AnimatedButton>
                </div>
            </main>

            {/* modalka */}
            {isOpen && (
                <div 
                    className="absolute md:w-[400px] w-full h-[300px] bg-black z-50 
                                rounded-lg flex border-2 border-[#8f184f]
                                top-1/2 left-1/2 -translate-x-1/2 -translate-y-1/2"> 
                    <form 
                        className="relative flex flex-col justify-center items-center gap-2 mx-auto w-full"
                        onSubmit={(e) => {e.preventDefault()}}
                        >
                        <input
                            ref={idRef}
                            type="text" 
                            placeholder="id няшки" 
                            disabled={isReply}
                            className={`p-2 border-2 rounded-lg 
                                ${isReply 
                                    ? 'opacity-50'
                                    : 'opacity-100'
                                }`}
                            />
                        <input 
                            ref={textRef}
                            type="text" 
                            placeholder="Текст" 
                            className="p-2 border-2 rounded-lg "
                            />
                        <div className="flex justify-center items-center">
                            <input
                                type="checkbox" id="anon" name="anon"
                                checked={isAnon}
                                onChange={(e) => {setIsAnon(e.target.checked)}}
                            />
                            <label htmlFor="anon">{`анонимно (увидит только id)`}</label>
                        </div>
                        

                        <button 
                        className="bg-[#8f184f] text-white px-4 py-2 rounded-lg"
                        type="button" onClick={handleSendMessage}>Отправить</button>

                        <button onClick={() => {setIsOpen(false)}} className="absolute top-1 right-2">X</button>
                    </form>
                </div>
            )}
        </div>
    )

}


function AnimatedButton({ 
    children,
    openModal 
}: {
    children: React.ReactNode,
    openModal: () => void

}) {
    const canvasRef = useRef<HTMLCanvasElement>(null);
    const buttonRef = useRef<HTMLButtonElement>(null);
    const animationRef = useRef<number>(0);
    const isHovering = useRef(false);

    const sizeRef = useRef({w:0,h:0})

    useEffect(() => {
        const canvas = canvasRef.current
        const button = buttonRef.current
        if(!canvas || !button) return;

        const ctx = canvas.getContext("2d");
        if(!ctx) return;

        const updateSize = () => {
            const rect = canvas.parentElement!.getBoundingClientRect();
            const dpr = window.devicePixelRatio || 1;

            sizeRef.current.w = rect.width;
            sizeRef.current.h = rect.height;

            canvas.width = rect.width * dpr;
            canvas.height = rect.height * dpr;
            canvas.style.width = rect.width + "px"
            canvas.style.height = rect.height + "px"
            ctx.setTransform(dpr,0,0,dpr,0,0)
        }
        updateSize()

        function getBtnCenter(){
            const btn = button!.getBoundingClientRect()
            const cvs = canvas!.getBoundingClientRect()
            return{
                x: cvs.width/2,
                y: cvs.height/2
            }
        }

        const particles: {
            x: number,
            y: number,
            radius: number,
            alpha: number,
            life: number,
            vx: number,
            vy: number
        }[] = []

        const speed = 50
        let lastTime = performance.now()

        function animate(currentTime: number) {
            if (!ctx || !canvas) return
            const deltaTime = (currentTime - lastTime) / 1000
            lastTime = currentTime

            const {w , h} = sizeRef.current
            ctx.clearRect(0,0, w, h)

            
            if(Math.random() < 0.06) {
                const angle = Math.random() * Math.PI * 2
                particles.push({
                    x: Math.random() * w,
                    y: Math.random() * h,
                    radius: 4,
                    alpha: 1,
                    life: 1,
                    vx: Math.cos(angle) * speed,
                    vy: Math.sin(angle) * speed
                })
            }
            const target = getBtnCenter()

            for (let i = particles.length -1; i >= 0; i--) {
                const p = particles[i];

                p.life -= deltaTime * 0.3;
                p.alpha = p.life
                
                if(isHovering.current) {
                    const dx = target.x - p.x
                    const dy = target.y - p.y
                    const dist = Math.sqrt(dx * dx + dy * dy) + 0.001

                    const force = Math.max(0, 1 - dist / 700) * 400;

                    p.vx += (dx / dist) * force *deltaTime
                    p.vy += (dy / dist) * force *deltaTime
                } 

                // max speed
                const speed = Math.hypot(p.vx, p.vy);
                const maxSpeed = 160;
                if (speed > maxSpeed) {
                    p.vx = (p.vx / speed) * maxSpeed;
                    p.vy = (p.vy / speed) * maxSpeed;
                }

                p.x += p.vx * deltaTime;
                p.y += p.vy * deltaTime;

                

                if (p.alpha <= 0) {
                    particles.splice(i, 1)
                    continue
                }
                

                ctx.globalAlpha = p.alpha;
                ctx.shadowBlur = 1;
                ctx.shadowColor = "white";
                ctx.fillStyle = 'white';

                ctx.beginPath();
                ctx.arc(p.x, p.y, p.radius, 0, Math.PI * 2);
                ctx.fill();
            }
            ctx.globalAlpha = 1
            animationRef.current = requestAnimationFrame(animate)
        }

        animationRef.current = requestAnimationFrame(animate)
        window.addEventListener('resize', updateSize)

        return () => {
            if (animationRef.current){
                cancelAnimationFrame(animationRef.current)
            }
            window.removeEventListener('resize', updateSize)
        }
    }, [])

    return (
        <div className="relative flex justify-center items-center h-full w-full">
            <canvas
                ref={canvasRef}
                className="absolute w-full h-full pointer-events-none z-1"
            >
            </canvas>
            <button
                ref={buttonRef}
                onClick={openModal}
                onMouseEnter={() => (isHovering.current = true)}
                onMouseLeave={() => (isHovering.current = false)}
                className="relative px-8 py-4 bg-white/10 rounded-md hover:bg-white/40 transition-colors z-2"
            >
                {children}
            </button>

        </div>
    )
}
