"use client"

import { useState, useEffect } from "react"
import { getInboxMessages, getSentMessages, sendMessage, getCurrentMessage } from "../utils/messages"

export function useMessages(userId: string | null) {
    const [inbox, setInbox] = useState<any[]>([])
    const [sent, setSent] = useState<any[]>([])
    const [loading, setLoading] = useState(true)

    const loadMessage = async () => {
        if(!userId) return

        try{
            setLoading(true)
            const [inboxData, sentData] = await Promise.all([
                getInboxMessages(userId), 
                getSentMessages(userId)
            ])

            setInbox(inboxData)
            setSent(sentData)
        } catch (error) {
            console.error("Error loading messages:", error)
        } finally {
            setLoading(false)
        }
    }

    useEffect(() => {
        loadMessage()
    },[userId])

    const send = async (
        to: {publicId?: string, userId?: string}, 
        body: string, 
        isAnon: boolean
    ) => {
        if(!userId) throw new Error("User not logged in")
        
        await sendMessage({
            fromUserId: userId,
            toUserId: to.userId,
            toPublicId: to.publicId, 
            body, 
            isAnon
        })

        await loadMessage()
    }

    return {
        inbox,
        sent,
        loading,
        sendMessage: send,
        refresh: loadMessage
    }

}

export function useCurrentMessage(userId: string | null, messageId: string | null) {
    const [message, setMessage] = useState<any>(null)
    const [loading, setLoading] = useState(false)

    useEffect(() => {
        if (!userId || !messageId) return

        const loadMessage = async () => {
            setLoading(true)
            try{
                const data = await getCurrentMessage(messageId, userId)
                setMessage(data)
            } catch (error) {
                console.error("Error loading messages:", error)
            } finally {
                setLoading(false)
            }
        }
        loadMessage()

    }, [userId, messageId])

    return {
        message,
        loading
    }
}